const { ccclass, property } = cc._decorator;

@ccclass
export default class TelegramTest extends cc.Component {

    @property(cc.Label)
    lblTip: cc.Label = null;;
    // LIFE-CYCLE CALLBACKS:

    // onLoad () {}

    start() {
        let telegramSdk = window['Telegram'];
        if (telegramSdk) {
            let sdk = telegramSdk.WebApp;
            this.lblTip.string = sdk ? 'success' : 'fail';
        } else {
            this.lblTip.string = 'fail';
        }
    }

    // update (dt) {}
}
